import React, { useEffect } from 'react'
import { Table, Tag} from 'antd';
import { useDispatch, useSelector } from 'react-redux';
import { layDanhSachNguoiDungAction, xoaNguoiDungAction } from '../../../Redux/actions/QuanLyNguoiDungAction';

import { Fragment } from 'react';
import { NavLink } from 'react-router-dom';
import { EditOutlined, DeleteOutlined } from '@ant-design/icons';
import { Input } from 'antd';

export default function Users() {
  
    const dispatch = useDispatch()
    const { dsNguoiDung } = useSelector(state => state.QuanLyNguoiDungReducer)
    console.log(dsNguoiDung)
    useEffect(() => {
        dispatch(layDanhSachNguoiDungAction())
    }, [])
    

    const columns = [
        {
            title: 'Ti khon',
            dataIndex: 'taiKhoan',
            key: 'taiKhoan',
            render: text => <span>{text}</span>,
        },
        {
            title: 'H v tn',
            dataIndex: 'hoTen',
            key: 'hoTen',
        },
        {
            title: 'Email',
            dataIndex: 'email',
            key: 'email',
        },
        {
            title: 'S in thoi',
            dataIndex: 'soDt',
            key: 'soDt',
        },
        {
            title: 'Loi ngi dng',
            key: 'maLoaiNguoiDung',
            dataIndex: 'maLoaiNguoiDung',
            render: text => {
                let color = text ==='KhachHang' ? 'green' : 'volcano'
                let textRender=text
                if(text==='QuanTri'){
                    textRender= 'Qun tr'
                }else{
                    textRender='Khch hng'
                }
                return <Tag color = {color}>{textRender}</Tag>
            }
              
      
        },



        {
            title: 'Hnh ng',
            key: 'taiKhoan',
            render:    (text, nguoiDung) => {
                return <Fragment>
                    <NavLink className="text-blue-500 text-xl" to={`/admin/users/edituser`}><EditOutlined /></NavLink>
                    <span className=" text-red-500 text-xl ml-4 cursor-pointer" onClick={() => {
                        if (window.confirm('Bn c chc chn mun ngi dng ' + nguoiDung.taiKhoan)) {
                            dispatch(xoaNguoiDungAction(nguoiDung.taiKhoan))
                        }
                    }} ><DeleteOutlined /></span>
                    
                </Fragment>
            }
        },
    ];

    const { Search } = Input;
    const onSearch = value => {
        console.log(value)
        dispatch(layDanhSachNguoiDungAction(value))
      
    };
    const data = dsNguoiDung
    return (
        <div>
            <h1 className="text-2xl mb-5">Danh sch ngi dng</h1>
            <Search
                className="mb-5"
                placeholder="Nhp tn ngi dng"
                enterButton="Tm kim"
                size="large"

                onSearch={onSearch}
            />
            <Table columns={columns} dataSource={data} />

        </div>
    )
}
