import React from 'react'
import { Form, Input, Button, Select } from 'antd';
import { useDispatch } from 'react-redux';
import { useFormik } from 'formik';
import { GROUPID } from '../../../../Util/Settings/config';
import { themNguoiDungAction } from '../../../../Redux/actions/QuanLyNguoiDungAction';
export default function AddUser() {

    const { Option } = Select;
    const layout = {
        labelCol: {
            span: 3,
        },
        wrapperCol: {
            span: 16,
        },
    };
    const tailLayout = {
        wrapperCol: {
            offset: 8,
            span: 16,
        },
    };
   
    const handleChangeSelect = (value) => {
       
       formik.setFieldValue('maLoaiNguoiDung',value)
    }

    const dispatch = useDispatch()
    const formik = useFormik({
        initialValues: {
            taiKhoan: '',
            matKhau: '',
            email: '',
            soDt: '',
            maNhom: '',
            maLoaiNguoiDung: '',
            hoTen: ''

        },
        onSubmit: (values) => {
            values.maNhom = GROUPID
            console.log('values', values)
           dispatch(themNguoiDungAction(values))

        }
    })


    return (
        <div>
            <h1 className="text-2xl font-bold mb-5">Thm ngi dng</h1>
            <Form {...layout}
                onSubmitCapture={formik.handleSubmit}
            >
                <Form.Item
                    name="taiKhoan"
                    label="Ti khon"
                    rules={[
                        {
                            required: true,
                        },
                    ]}
                    onChange={formik.handleChange}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    name="matKhau"
                    label="Mt khu"
                    rules={[
                        {
                            required: true,
                        },
                    ]}
                    onChange={formik.handleChange}
                >
                    <Input type="password" />
                </Form.Item>
                <Form.Item
                    name="email"
                    label="Email"
                    rules={[
                        {
                            required: true,
                        },
                    ]}
                    onChange={formik.handleChange}
                >
                    <Input type="email" />
                </Form.Item>
                <Form.Item
                    name="hoTen"
                    label="H v tn"
                    rules={[
                        {
                            required: true,
                        },
                    ]}
                    onChange={formik.handleChange}
                >
                    <Input />
                </Form.Item>
                <Form.Item
                    name="soDt"
                    label="S in thoi"
                    rules={[
                        {
                            required: true,
                        },
                    ]}
                    onChange={formik.handleChange}
                >
                    <Input type="number" />
                </Form.Item>
                <Form.Item
                    name="maLoaiNguoiDung"
                    label="Loi ngi dng"
                    rules={[
                        {
                            required: true,
                        },
                    ]}
                >
                    <Select
                        placeholder="Chn loi ngi dng"
                        onChange={handleChangeSelect}
                        allowClear
                    >
                        <Option value="KhachHang">Khch Hng</Option>
                        <Option value="QuanTri">Qun tr</Option>

                    </Select>
                </Form.Item>

                <Form.Item {...tailLayout}>
                    <Button type="primary" className="mr-5" htmlType="submit">
                        Thm ngi dng
                    </Button>


                </Form.Item>
            </Form>
        </div>
    )
}
